.hero-header-section {
  background-color: #ffb8ca;
  background-image: -webkit-image-set(url("pink-circle-m.7cf4a023.png"), url("pink-circle-m@2x.920eb986.png") 2x);
  background-image: image-set("pink-circle-m.7cf4a023.png", "pink-circle-m@2x.920eb986.png" 2x);
  background-position: 36px 83px;
  background-repeat: no-repeat;
  padding: 8px 20px 445px;
  position: relative;
}

.global-header {
  animation: slideInFromTop 1s ease-out;
}

.global-header .container {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.global-header__logo {
  width: 199px;
  height: 50px;
}

.global-header__menu {
  gap: 35px;
  display: none;
}

.global-header__menu a {
  color: #fff;
  font-family: DM Sans;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 21px;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

.global-header__menu a:hover, .global-header__menu a:focus {
  color: #d41443;
  cursor: pointer;
}

.global-header__button {
  width: 159px;
  height: 44px;
  color: #d41443;
  background-color: #fff;
  border: none;
  border-radius: 22px;
  align-items: center;
  padding-left: 37px;
  font-family: DM Sans;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 21px;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
  display: none;
}

.global-header__button:focus, .global-header__button:hover {
  cursor: pointer;
  background-color: #ecebeb;
}

.global-header__button svg {
  color: #d41443;
  height: 8px;
  width: 4px;
  margin-left: 12px;
}

.global-header .button--toggle-menu {
  color: #d41443;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  padding: 0;
  top: 50px;
  left: 420px;
}

.global-header .button--toggle-menu svg {
  width: 20px;
  height: 12px;
